import React from 'react';
import HeaderLink from './HeaderNavLink';
import classNames from 'classnames';
import './headernav.scss';

export default function({ className }){
    return (
        <div className={classNames("header-nav", { [className]: !!className })}>
            <HeaderLink 
                title="Home"
                icon="/assets/images/header/home-icon.png"
                href="#"
                margined
            />
            <HeaderLink 
                title="About Us"
                icon="/assets/images/header/about-icon.png"
                href="#"
                margined
            />
            <HeaderLink 
                title="Roadmap"
                icon="/assets/images/header/roadmap-icon.png"
                href="#"
                margined
            />
            <HeaderLink 
                title="Utility"
                icon="/assets/images/header/utility-icon.png"
                href="#"
            />            
        </div>
    );
}