import React from 'react';
import { Redirect, Route, Switch } from 'react-router';
import Main from './pages/Main';

export default function(){
    return (
        <div className="page">            
            <picture>
                <source srcSet="/assets/images/figures/lights.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/lights@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/lights@2x.png 2x" type="image/png" />
                <img src="/assets/images/figures/lights.png" alt="lights" className="page__lights page__lights_fst" />
            </picture>           
            <picture>
                <source srcSet="/assets/images/figures/lights.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/lights@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/lights@2x.png 2x" type="image/png" />
                <img src="/assets/images/figures/lights.png" alt="lights" className="page__lights page__lights_scd" />
            </picture>   
            <div className="page__background"></div>
            <Switch>
                <Route path="/" component={Main} exact />
                <Redirect to="/" />
            </Switch>
        </div>
    );
}