import React from 'react';
import classNames from 'classnames';
import './gbutton.scss';

export default function({ className, icon, text, glassIcon, onClick }){
    return (
        <button 
            onClick={onClick} 
            className={classNames("gbutton", { [className]: !!className })}
        >
            <span className="gbutton__wrapper">
                {icon && <img 
                    src={icon}
                    alt="icon"
                    className="gbutton__icon"
                />}
                <i className="gbutton__text">{text}</i>
                <img 
                    src={glassIcon ? glassIcon : "/assets/images/glass.svg"}
                    alt="glass"
                    className="gbutton__glass" 
                />
            </span>
        </button>
    );
}