import React from 'react';
import GButton from '../GButton/GButton';
import './intro.scss';

export default function(){
    return (
        <div className="intro">
            <div className="intro__content">
                <h1>About Whalies NFT</h1>
                <p>
                    10.000 uniquely generated Whalies that have igrated to the cleaner and 
                    warmer network of Solana. Drawn by hand and assemmbled by code,
                    not one Whale is the same!
                </p>
                <GButton 
                    text="Mint"
                    className="intro__btn"
                />
            </div>
            <div className="intro__background">
                <picture>
                    <source srcSet="/assets/images/intro/sea@mobile.webp" media="(max-width: 665px)" type="image/webp" />
                    <source srcSet="/assets/images/intro/sea@mobile.png" media="(max-width: 665px)" type="image/png" />
                    <source srcSet="/assets/images/intro/sea.webp" type="image/webp" />
                    <source srcSet="/assets/images/intro/sea@3x.png 3x" type="image/png" />
                    <source srcSet="/assets/images/intro/sea@2x.png 2x" type="image/png" />
                    <img src="/assets/images/intro/sea.png" alt="content" className="back" />
                </picture>
                <picture>
                    <source srcSet="/assets/images/intro/palm-left.webp" type="image/webp" />
                    <source srcSet="/assets/images/intro/palm-left@3x.png 3x" type="image/png" />
                    <source srcSet="/assets/images/intro/palm-left@2x.png 2x" type="image/png" />
                    <img src="/assets/images/intro/palm-left.png" alt="palm-left" className="palm-left" />
                </picture>
                <picture>
                    <source srcSet="/assets/images/intro/palm-left.webp" type="image/webp" />
                    <source srcSet="/assets/images/intro/palm-left@3x.png 3x" type="image/png" />
                    <source srcSet="/assets/images/intro/palm-left@2x.png 2x" type="image/png" />
                    <img src="/assets/images/intro/palm-left.png" alt="palm-left" className="palm-right" />
                </picture>
                <picture>
                    <source srcSet="/assets/images/placeholder.png" media="(max-width: 768px)" type="image/jpg" />                    
                    <source srcSet="/assets/images/intro/palm-blur@3x.png 3x" type="image/png" />
                    <source srcSet="/assets/images/intro/palm-blur@2x.png 2x" type="image/png" />
                    <img src="/assets/images/intro/palm-blur.png" alt="palm-blur" className="palm-blur" />
                </picture>
                <picture>
                    <source srcSet="/assets/images/intro/sun@mobile.webp" media="(max-width: 665px)" type="image/webp" />
                    <source srcSet="/assets/images/intro/sun@mobile.png" media="(max-width: 665px)" type="image/png" />
                    <source srcSet="/assets/images/intro/sun.webp" type="image/webp" />
                    <source srcSet="/assets/images/intro/sun@3x.png 3x" type="image/png" />
                    <source srcSet="/assets/images/intro/sun@2x.png 2x" type="image/png" />
                    <img src="/assets/images/intro/sun.png" alt="sun" className="sun" />
                </picture>
                <div className="intro__dolphins">
                    <picture>
                        <source srcSet="/assets/images/intro/dolphin-left.webp" type="image/webp" />
                        <source srcSet="/assets/images/intro/dolphin-left@3x.png 3x" type="image/png" />
                        <source srcSet="/assets/images/intro/dolphin-left@2x.png 2x" type="image/png" />
                        <img src="/assets/images/intro/dolphin-left.png" alt="dolphin-left" className="dolphin-left" />
                    </picture>
                    <picture>
                        <source srcSet="/assets/images/intro/dolphin-middle.webp" type="image/webp" />
                        <source srcSet="/assets/images/intro/dolphin-middle@3x.png 3x" type="image/png" />
                        <source srcSet="/assets/images/intro/dolphin-middle@2x.png 2x" type="image/png" />
                        <img src="/assets/images/intro/dolphin-middle.png" alt="dolphin-middle" className="dolphin-middle" />
                    </picture>
                    <picture>
                        <source srcSet="/assets/images/intro/dolphin-right.webp" type="image/webp" />
                        <source srcSet="/assets/images/intro/dolphin-right@3x.png 3x" type="image/png" />
                        <source srcSet="/assets/images/intro/dolphin-right@2x.png 2x" type="image/png" />
                        <img src="/assets/images/intro/dolphin-right.png" alt="dolphin-right" className="dolphin-right" />
                    </picture>
                </div>
            </div>
        </div>
    );
}