import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';

export default function(breakpoints){
    if(typeof breakpoints !== 'object'){
        throw Error("Breakpoints must be Object!");
    }

    const [size, setSize]       = useState(window.innerWidth);
    const [current, setCurrent] = useState(null);

    function checkValue(){
        const currentSize = window.innerWidth;
        const less = parseInt(breakpoints["<"]);
        const more = parseInt(breakpoints[">"]);

        if(!isNaN(less)){
            if(currentSize < less && (current === false || current === null)){
                setCurrent(true);
                setSize(currentSize);
            } else if(currentSize > less && current === true){
                setCurrent(false);
                setSize(currentSize);
            }
        } else if (!isNaN(more)){
            if(currentSize > more && (current === false || current === null) ){
                setCurrent(true);
                setSize(currentSize);
            } else if(currentSize < more && current === true){
                setCurrent(false);
                setSize(currentSize);
            }
        }
    }

    useEffect(() => {
        checkValue();
    }, []);

    useEffect(() => {
        window.addEventListener("resize", checkValue);

        return () => window.removeEventListener("resize", checkValue);
    }, [current]);

    return [current, size];
}