import React from 'react';
import Title from '../Title/Title';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css'; 
import './team.scss';

const list = [
    {
        id: 0,
        name: "0x_username",
        prof: "CEO, Designer",
        image: "/assets/images/team/team-01.jpg"
    },
    {
        id: 1,
        name: "0x_username",
        prof: "CEO, Designer",
        image: "/assets/images/team/team-02.jpg"
    },
    {
        id: 2,
        name: "0x_username",
        prof: "CEO, Designer",
        image: "/assets/images/team/team-03.jpg"
    },
    {
        id: 3,
        name: "0x_username",
        prof: "CEO, Designer",
        image: "/assets/images/team/team-04.jpg"
    },
    {
        id: 4,
        name: "0x_username",
        prof: "CEO, Designer",
        image: "/assets/images/team/team-05.jpg"
    },
]

export default function(){
    const settings = {
        slidesPerView: 4,
        spaceBetween: 0,
        grabCursor: true,
        centerSlider: true,
        loop: true,
        breakpoints: {
            1110: {
                slidesPerView: 5,
            },
        }
    };

    return (
        <div className="team">
            <picture>
                <source srcSet="/assets/images/figures/team-reef-left.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/team-reef-left@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/team-reef-left@2x.png 2x" type="image/png" />
                <img src="/assets/images/figures/team-reef-left.png" alt="seaweed" className="team__ref-left" />
            </picture>
            <picture>
                <source srcSet="/assets/images/figures/team-reef-right.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/team-reef-right@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/team-reef-right@2x.png 2x" type="image/png" />
                <img src="/assets/images/figures/team-reef-right.png" alt="seaweed" className="team__ref-right" />
            </picture>
            <img src="/assets/images/figures/fishes-blur.svg" alt="fishes" className="team__fishes-blur" />
            <Title 
                text="Our Team"
                className="team__title"
                star
            />    
            <Swiper className="team__swiper" {...settings}>
                {list.map(item => (
                    <SwiperSlide>                            
                        <div className="team__item" key={item.id}>
                            <div className="team__wrapper">
                                <div className="team__image">
                                    <img 
                                        src={item.image} 
                                        alt="avatar" 
                                    />
                                </div>
                                <span className="team__name">{item.name}</span>
                                <span className="team__prof">{item.prof}</span>
                            </div>
                        </div>
                    </SwiperSlide>
                ))}                    
            </Swiper>
        </div>
    );
}