import React from 'react';
import GButton from '../GButton/GButton';
import Title from '../Title/Title';
import './community.scss';

export default function(){
    return (
        <div className="community">
            <picture>
                <source srcSet="/assets/images/community/background.webp" type="image/webp" />
                <source srcSet="/assets/images/community/background@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/community/background@3x.png 2x" type="image/png" />
                <img src="/assets/images/community/background.png" alt="community" className="community__background" />
            </picture>            
            <img src="/assets/images/figures/fishes-blur.svg" alt="fishes" className="community__fishes-blur" />
            <div className="container community__container">                
                <div className="community__content">
                    <Title 
                        text="Join Our Community"
                        className="community__title"
                    />
                    <div className="community__controls">
                        <GButton
                            icon="/assets/images/community/discord.svg"
                            text="Discord"
                            className="community__btn community__btn_dark"
                        />
                        <GButton
                            icon="/assets/images/community/telegram.svg"
                            text="Telegram"
                            className="community__btn community__btn_light"
                        />
                        <GButton
                            icon="/assets/images/community/twitter.svg"
                            text="Twitter"
                            className="community__btn community__btn_light"
                        />
                    </div>
                </div>
            </div>
        </div>
    );
}