import React, { useState, useEffect } from 'react';
import Title from '../Title/Title';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css'; 
import './whalies3d.scss';
import useResize from '../../hooks/useResize';

export default function(){
    const [isTrue] = useResize({ "<": 769 });

    const settings = {
        slidesPerView: 2,
        loop: window.innerWidth < 769,
        centeredSlides: true,
        breakpoints: {
            769: {
                slidesPerView: 3,
                centeredSlides: false
            }
        }
    };

    return (
        <div className="whalies">
            <picture>
                <source srcSet="/assets/images/placeholder.png" media="(max-width: 768px)" type="image/png" />
                <source srcSet="/assets/images/figures/whalies-reef-left.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/whalies-reef-left@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/whalies-reef-left@2x.png 2x" type="image/png" />
                <img src="/assets/images/figures/whalies-reef-left.png" alt="reef" className="whalies-reef-left" />
            </picture>
            <picture>
                <source srcSet="/assets/images/figures/whalies-reef-right@mobile.webp" media="(max-width: 768px)" type="image/webp" />
                <source srcSet="/assets/images/figures/whalies-reef-right@mobile.png" media="(max-width: 768px)" type="image/png" />
                <source srcSet="/assets/images/figures/whalies-reef-right.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/whalies-reef-right@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/whalies-reef-right@2x.png 2x" type="image/png" />
                <img src="/assets/images/figures/whalies-reef-right.png" alt="reef" className="whalies-reef-right" />
            </picture>
            <div className="whalies__block">
                <div className="container whalies__container">
                    <Title 
                        text="Meet 3D Whalies Soon..."
                        className="whalies__title"
                    >
                        <img 
                            src="/assets/images/figures/fishes.png" 
                            alt="fishes" 
                            className="fishes"
                        />
                    </Title>
                    <div className="whalies__slider">
                        <Swiper {...settings}>
                            <SwiperSlide>                            
                                <div className="whalies__item">
                                    <div className="whalies__wrapper">
                                        <picture>
                                            <source srcSet="/assets/images/whalies3d/dolphin-y.webp" type="image/webp" />
                                            <source srcSet="/assets/images/whalies3d/dolphin-y@3x.png 3x" type="image/png" />
                                            <source srcSet="/assets/images/whalies3d/dolphin-y@2x.png 2x" type="image/png" />
                                            <img src="/assets/images/whalies3d/dolphin-y.png" alt="dolphing" />
                                        </picture>
                                        <p className="whalies__text">Wheel 1</p>
                                    </div>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide>
                                <div className="whalies__item">
                                    <div className="whalies__wrapper">                                
                                        <picture>
                                            <source srcSet="/assets/images/whalies3d/dolphin-v.webp" type="image/webp" />
                                            <source srcSet="/assets/images/whalies3d/dolphin-v@3x.png 3x" type="image/png" />
                                            <source srcSet="/assets/images/whalies3d/dolphin-v@2x.png 2x" type="image/png" />
                                            <img src="/assets/images/whalies3d/dolphin-v.png" alt="dolphing" />
                                        </picture>
                                        <p className="whalies__text">Wheel 2</p>
                                    </div>
                                </div>
                            </SwiperSlide>
                            <SwiperSlide>
                                <div className="whalies__item">
                                    <div className="whalies__wrapper">                                
                                        <picture>
                                            <source srcSet="/assets/images/whalies3d/dolphin-b.webp" type="image/webp" />
                                            <source srcSet="/assets/images/whalies3d/dolphin-b@3x.png 3x" type="image/png" />
                                            <source srcSet="/assets/images/whalies3d/dolphin-b@2x.png 2x" type="image/png" />
                                            <img src="/assets/images/whalies3d/dolphin-b.png" alt="dolphing" />
                                        </picture>
                                        <p className="whalies__text">Wheel 3</p>
                                    </div>
                                </div>                            
                            </SwiperSlide>
                        </Swiper>
                    </div>
                </div>
            </div>
        </div>
    );
}