import React from 'react';
import { NavLink } from 'react-router-dom';

export default function({ title, icon, href, margined }){
    return (
        <React.Fragment>
            <NavLink className="header-nav__link" to={href}>
                {icon && <img 
                    src={icon} 
                    alt="icon" 
                />}
                <span>{title}</span>
            </NavLink>
            {margined && <i className="header-nav__margined"></i>}
        </React.Fragment>
    );
}