import React from 'react';
import classNames from 'classnames';
import './title.scss';

export default function({ className, text, star, children }){
    return (
        <h2 className={classNames("title", {[className]: !!className})}>
            <span>
                {text}
            </span>
            {star && <img 
                src="/assets/images/star.svg" 
                alt="star" 
            />}
            {children}
        </h2>
    );
}