import React from 'react';
import GButton from '../GButton/GButton';
import Title from '../Title/Title';
import './utilies.scss';

const list = [
    {
        "id": 0,
        "icon": "/assets/images/utilies/fish-icon.png",
        "text": "10% of minting reflection"
    },
    {
        "id": 1,
        "icon": "/assets/images/utilies/glasses-icon.png",
        "text": "7.5% of royalty fees to DAO wallet and 2.5% cut by Opensea"
    },
    {
        "id": 2,
        "icon": "/assets/images/utilies/cactus.svg",
        "text": "Sweep Floor price when the community wants (TBD)"
    },
    {
        "id": 3,
        "icon": "/assets/images/utilies/diamante.svg",
        "text": "Reward distribution to holders with royalty fees from DAO wallet every month"
    },
]

export default function(){
    return (
        <div className="utilies">
            <div className="container utilies__container">
                <div className="utilies__left">
                    <Title 
                        text="Utilies"
                        className="utilies__title"
                        star
                    />
                    <img 
                        src="/assets/images/figures/scribble.svg" 
                        alt="scribble" 
                        className="utilies__scribble"
                    />
                    <picture>
                        <source srcSet="/assets/images/placeholder.png" media="(max-width: 768px)" type="image/png" />
                        <source srcSet="/assets/images/utilies/fish.webp" type="image/webp" />
                        <source srcSet="/assets/images/utilies/fish@3x.png 3x" type="image/png" />
                        <source srcSet="/assets/images/utilies/fish@2x.png 2x" type="image/png" />
                        <img src="/assets/images/utilies/fish.png" alt="fish" className="utilies__fish" />
                    </picture>
                </div>
                <div className="utilies__right">
                    <div className="utilies__wrapper">
                        <picture>
                            <source srcSet="/assets/images/utilies/seaweed.webp" type="image/webp" />
                            <source srcSet="/assets/images/utilies/seaweed@3x.png 3x" type="image/png" />
                            <source srcSet="/assets/images/utilies/seaweed@2x.png 2x" type="image/png" />
                            <img src="/assets/images/utilies/seaweed.png" alt="seaweed" className="utilies__seaweed" />
                        </picture>
                        <picture>
                            <source srcSet="/assets/images/utilies/fish.webp" type="image/webp" />
                            <source srcSet="/assets/images/utilies/fish@3x.png 3x" type="image/png" />
                            <source srcSet="/assets/images/utilies/fish@2x.png 2x" type="image/png" />
                            <img src="/assets/images/utilies/fish.png" alt="fish" className="utilies__fish-right" />
                        </picture>
                        <div className="utilies__paper">
                            <div className="utilies__list">
                                {list.map(item => (                                    
                                    <div className="utilies__item" key={item.id}>
                                        <div className="utilies__item-icon">
                                            <img src={item.icon} alt="icon" />
                                        </div>
                                        <p className="utilies__item-text">{item.text}</p>
                                    </div>
                                ))}

                                <GButton 
                                    text="Mint"
                                    className="utilies__item-btn"
                                />
                            </div>
                        </div>
                        <p className="utilies__posttext">
                            <span>As the Whalies world grows, more utility will be added</span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
}