import React from 'react';
import Title from '../Title/Title';
import * as SVG from '../SVG';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css'; 
import './roadmap.scss';

const phases = { 
    "phase1": [ 
        "Create Whalies branding",
        "Create and form Whalies DAO",
        "Create Whalies DAO wallets (Multi-Sig)",
        "Launch Whalies collection site",
        "Launch 10K 2D Whalies collection",
        "30% of the revenue will go to Whalies DAO wallet"
    ],
    "phase2": [
        "Launch 6666 3D Whalies collection",
        "Airdrop 50% of 3D Whalies to all 2D Whalies minters",
        "Other 50% of the collection for public sale",
        "50% of the revenue will go to Whalies DAO wallet"
    ],
    "phase3": [
        "Launch 6666 3D Whalies collection",
        "Airdrop 50% of 3D Whalies to all 2D Whalies minters",
        "Other 50% of the collection for public sale",
        "50% of the revenue will go to Whalies DAO wallet"
    ]
}

export default function(){
    const settigs = {
        slidesPerView: 4,
        direction: "vertical"
    };

    return (
        <div className="roadmap">
            <img 
                src="/assets/images/figures/fishes-blur.svg" 
                alt="fishes-blur" 
                className="roadmap__fishes-blur"
            />
            <picture>
                <source srcSet="/assets/images/figures/roadmap-reef.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/roadmap-reef@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/roadmap-reef@3x.png 2x" type="image/png" />
                <img src="/assets/images/figures/roadmap-reef.png" alt="reef" className="roadmap__reef roadmap__reef_01" />
            </picture>
            <picture>
                <source srcSet="/assets/images/figures/roadmap-reef.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/roadmap-reef@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/roadmap-reef@3x.png 2x" type="image/png" />
                <img src="/assets/images/figures/roadmap-reef.png" alt="reef" className="roadmap__reef roadmap__reef_02" />
            </picture>
            <picture>
                <source srcSet="/assets/images/figures/roadmap-reef.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/roadmap-reef@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/roadmap-reef@3x.png 2x" type="image/png" />
                <img src="/assets/images/figures/roadmap-reef.png" alt="reef" className="roadmap__reef roadmap__reef_03" />
            </picture>
            <picture>
                <source srcSet="/assets/images/figures/roadmap-reef.webp" type="image/webp" />
                <source srcSet="/assets/images/figures/roadmap-reef@3x.png 3x" type="image/png" />
                <source srcSet="/assets/images/figures/roadmap-reef@3x.png 2x" type="image/png" />
                <img src="/assets/images/figures/roadmap-reef.png" alt="reef" className="roadmap__reef roadmap__reef_04" />
            </picture>    
            <picture>
                <source srcSet="/assets/images/figures/bubles.webp" type="image/webp" />
                <img src="/assets/images/figures/bubles.png" alt="bubles" className="roadmap__bubles" />
            </picture>

            <div className="container roadmap__container">
                <Title 
                    text="Roadmap"
                    className="roadmap__title"
                    star
                />
                <Swiper className="roadmap__slider" {...settigs}>

                    {/* FIRST SLIDE */}
                    <SwiperSlide>
                        <div className="roadmap-item roadmap-item_first">
                            <div className="roadmap-item__wrapper">  
                                <img 
                                    src="/assets/images/roadmap/path-01.svg" 
                                    alt="path" 
                                    className="roadmap-item__path1"
                                />                                 
                                <Title
                                    text="Phase 1"
                                    className="roadmap-item__title"
                                >                            
                                    <SVG.Bubble 
                                        className="roadmap-item__title-bubble"
                                    />
                                </Title>
                                <div className="roadmap-item__paper">
                                    <ul className="roadmap-item__list">
                                        {phases["phase1"].map(item => (
                                            <li className="roadmap-item__point" key={item}>
                                                <img src="/assets/images/listpoint.png" alt="." />
                                                <span>{item}</span>
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>

                    {/* SECOND SLIDE */}
                    <SwiperSlide>
                        <div className="roadmap-item roadmap-item_second">
                            <div className="roadmap-item__wrapper">  
                                <img 
                                    src="/assets/images/roadmap/path-02.svg" 
                                    alt="path" 
                                    className="roadmap-item__path2"
                                />                              
                                <Title
                                    text="Phase 2"
                                    className="roadmap-item__title"
                                >                            
                                    <SVG.Bubble 
                                        className="roadmap-item__title-bubble"
                                    />
                                </Title>
                                <div className="roadmap-item__paper">
                                    <ul className="roadmap-item__list">
                                        {phases["phase2"].map(item => (
                                            <li className="roadmap-item__point" key={item}>
                                                <img src="/assets/images/listpoint.png" alt="." />
                                                <span>{item}</span>
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </SwiperSlide>
                    
                    {/* THIRD SLIDE */}
                    <SwiperSlide>
                        <div className="roadmap-item roadmap-item_third">
                            <div className="roadmap-item__wrapper">  
                                <img 
                                    src="/assets/images/roadmap/path-03.svg" 
                                    alt="path" 
                                    className="roadmap-item__path3"
                                />                          
                                <Title
                                    text="Phase 3"
                                    className="roadmap-item__title"
                                >                            
                                    <SVG.Bubble 
                                        className="roadmap-item__title-bubble"
                                    />
                                </Title>
                                <div className="roadmap-item__paper">
                                    <ul className="roadmap-item__list">
                                        {phases["phase3"].map(item => (
                                            <li className="roadmap-item__point" key={item}>
                                                <img src="/assets/images/listpoint.png" alt="." />
                                                <span>{item}</span>
                                            </li>
                                        ))}
                                    </ul>
                                </div>
                            </div>
                        </div>                        
                    </SwiperSlide>
                </Swiper>
            </div>
        </div>
    );
}